import React, {useState, useEffect} from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  // Link
} from "react-router-dom";
import Header from "./components/Header";
import Footer from "./components/Footer";
import { MainMenu } from "./components/navigations/MainMenu";
import { Content } from "./components/dashboards/content/Content";
import { Main } from "./components/dashboards/main/Main";
import { Screen } from "./components/dashboards/screen/Screen";

function App() {
  const [screens, setScreens] = useState([])
  const [contents, setContents] = useState([])
  const [isLoadedScreens, setIsLoadedScreens] = useState(false)
  const [isLoadedContents, setIsLoadedContents] = useState(false)

  useEffect(() => {
    fetch('http://localhost:8000/api/screen')
      .then(res => res.json())
      .then(
        (result) => {
          setIsLoadedScreens(true);
          setScreens(result);
        },
        // (error) => {
        //   // setIsLoaded(true);
        //   // setError(error);
        // }
      )
  }, []);

  useEffect(() => {
    fetch('http://localhost:8000/api/content')
      .then(res => res.json())
      .then(
        (result) => {
          setIsLoadedContents(true);
          setContents(result);
        },
        // (error) => {
        //   // setIsLoaded(true);
        //   // setError(error);
        // }
      )
  }, []);

  return (
    <div className='d-flex flex-column min-vh-100'>
      <Header />

      <Router>
        <MainMenu />

        <Switch>
            <Route path="/content">
              <Content 
                contents={contents}
                setContents={setContents}
              />
            </Route>

            <Route path="/screen">
              <Screen 
                screens={screens}
                setScreens={setScreens}
              />
            </Route>

            <Route path="/">
              <Main  
                contents={contents}
                screens={screens}
                isLoadedContents={isLoadedContents}
                isLoadedScreens={isLoadedScreens}
              />
            </Route>
        </Switch>
      </Router>

      <Footer />
  </div> 
  );
}

export default App;