import { React } from "react";
import { TableRow } from "./TableRow";

export function TableBody(props) {

    return (
        <tbody>
            {props.screens.map((item, index) => {
                return <TableRow
                    key={item.id}
                    item={item}
                    index={index}
                    screens={props.screens}
                    contents={props.contents}
                />
            })}
        </tbody>
    )
}