import React from 'react';
import { Link } from "react-router-dom";

export function MainMenuItem({item, onClick, id}) {
    return (
        <li className = {`nav-item mr-3 ${ item.checked ? 'active' : '' }`}>
            <Link
                id={ id }
                className="nav-link" 
                to = { item.url } 
                onClick={ onClick }
            >{ item.title }</Link>
        </li>
    )
}