import React from 'react';

export function TableHead(props) {
    return(
        <thead>
            <tr>
                {props.tableHead.map((elem, index) => {
                    return (
                        <th key={index} className='text-center' scope="col">{elem}</th>
                    )
                })}      
            </tr>
        </thead>
    )
}