import React, { useEffect, useState } from 'react';
import { MainMenuItem } from "./MainMenuItem";

export function MainMenu() {
    const [menu, setMenu] = useState([
        { id: 1, checked: false, title: 'Главная', url: '/' },
        { id: 2, checked: false, title: 'Добавить URL', url: '/content' },
        { id: 3, checked: false, title: 'Добавить экран', url: '/screen' }
    ])

    useEffect(() => {
        const checkedItem = menu.map((item) => {
            if(item.url === window.location.pathname){
                item.checked = !item.checked
            }
    
            return item
        })

        setMenu(checkedItem)
        // eslint-disable-next-line
    }, [])

    const handleClick = (index) => {
        let newState = menu.map((item) => {
            item.checked = false;
            // eslint-disable-next-line
            if (item.id == index.target.id) {
                item.checked = !item.checked;
            }

            return item
        })

        setMenu(newState)
    }

    return (
        <nav className = "navbar navbar-expand navbar-dark bg-dark">
            <div className = "container collapse navbar-collapse">
                <ul className = "navbar-nav mr-auto">
                    { menu.map(item => {
                        return <MainMenuItem
                            id = {item.id}
                            item = {item}
                            key = {item.id}
                            onClick = {handleClick}
                        />
                    })}
                </ul>
            </div>
        </nav>
    )
}