import React from 'react';

export function TableRow(props) {
    const contentDelete = (event, id) => {
        event.preventDefault()

        props.setContents(props.contents.filter(item => item.id !== id))

        // fetch(`/api/content/destroy/${id}`)
        // .then(response => {
        //     if(response.ok) {
        //         props.setContents(props.contents.filter(item => item.id !== id))
        //     }
        // }) 
    }

    return (
    <tr>
        <td className = 'text-center'>{props.index}</td>

        <td className = 'text-center'>{props.item.title}</td>

        <td className='text-center'>{props.item.url}</td>

        <td className ='text-center'>
            <a className ='btn btn-danger align-item-center' 
                href = '/'
                onClick = {(event) => contentDelete(event, props.item.id)}
            >Удалить</a>
        </td>       
    </tr>
    )
}