import React from 'react';
import { TableRow } from "./TableRow";

export function TableBody(props) {
    return (
        props.contents.map((item, index) => {
            return <TableRow
                key={index}
                index={index}
                item={item}
                contents={props.contents}
                setContents={props.setContents}
            />
        })
    )
}