import React, { useState } from "react";
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';

export function ContentForm(props) {
    const [data, setData] = useState({
        title: '',
        url: '',
        id: '122'
    })

    const handleInputChange = (event) => {
        const target = event.target;
        const value = target.value;
        const name = target.name;

        setData({
            ...data,
            [name]: value,
        })
    }

    const handleSubmit = (event) => {
        event.preventDefault()

        let ff = [...props.contents, data]
        
        props.setContents(ff)
        props.onHide();
        // fetch('/api/content/store', {
        //     method: 'POST',
        //     headers: {
        //         'Accept': 'application/json',
        //         'Content-Type': 'application/json'
        //     },
        //     body: JSON.stringify(data)
        // })
        // .then(response => response.json())
        // .then(response => {
        //     // this.props.contentAdd(this.state)
        //     this.props.onHide()
        // }) 
    } 

    return (
        <Form onSubmit={(e) => handleSubmit(e)}>
            <Form.Group controlId="formBasicName">
                <Form.Label>Название</Form.Label>
                <Form.Control
                    value={data.title}
                    onChange={handleInputChange}
                    name="title" 
                    type="text" 
                    placeholder="Название" />
            </Form.Group>

            <Form.Group controlId="formBasicAdress">
                <Form.Label>Адрес страницы</Form.Label>
                <Form.Control
                    value={data.url}
                    onChange={handleInputChange} 
                    name="url" 
                    type="text" 
                    placeholder="Адрес страницы" />
            </Form.Group>

            <Button variant="primary" type="submit">
                Сохранить контент
            </Button>
        </Form>
    )
}